.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
}

.modal {
  width: 320px;
  min-height: 449px;
  background: #ffe54f;
  border-radius: 30px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 458px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 458px;
  }
}

.backdrop.is-hidden .modal {
  transform: translate(-50%)scale(0);
}

.modal-close {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

