  border: 1px solid rgba(18, 18, 18, .12);
  border-radius: 30px;
  padding-left: 12px;
  transition: outline-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.form-imput::-ms-input-placeholder {
  color: #c4c4c4;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  line-height: 1.25;
}

