}

.form-imput:focus {
  outline-color: #7eb101;
}

.form-imput:not(:focus):not(:placeholder-shown):valid {
  border: 1px solid #7eb101;
}

.form-imput:not(:focus):not(:placeholder-shown):invalid + .error {
  display: block;
}

.form-imput:not(:focus):not(:placeholder-shown):invalid {
  border: 1px solid #ff705d;
}

